/* Styling Guide */

/* Font Styles:
Heading Font: Carter One (Regular 400)  -  font-family: 'Carter One', cursive;
Body Font: Montserrat (Regular 400)  -  font-family: 'Montserrat', sans-serif;
*/

/* Color Palette:
#ffff00 - Pokemon Yellow
#142b68 - Pokemon Dark Blue
#edfeff - Pokemon Sky Blue 
*/


/* GLOBAL */
html{
	font-size: 125%;
}
body {
  width: 100%;
  font-family: 'Montserrat', sans-serif;
  background-color: #edfeff;
  overflow-x: hidden;
}

button:disabled {
  opacity: 0.5;
}

.positionRelative {
  position: relative;
}

.positionAbsolute {
  position: absolute;
}



/* Header Component Styling */
.headerComponent{
	margin: 0;
	padding: 0;
	background: #edfeff;
  display: flex;
  justify-content: center;
  align-items: center;
  
	
}

.headerComponent img {
  width: 50px;
  margin-left: 30px;
  -webkit-animation: shake 2.5s cubic-bezier(.36,.07,.19,.97) 3 2s;
          animation: shake 2.5s cubic-bezier(.36,.07,.19,.97) 3 2s;
  z-index: 5;
}


h1 {
 
  font-family: 'Carter One', cursive;
  font-size: 2rem;
  font-weight: lighter;
  /* width: 50%; */
  text-align: center;
  border-radius: 50%;
 color: #142b68;
 text-align: center;
 text-shadow: #ffff00 2px 2px ;
 z-index: 4;
  
}

span{
	font-size:1rem;
	vertical-align: super;
}

.infoCircle{
	color: #142b68;
	top: 6%;
	left: 90%;
	cursor: pointer;
	font-size: 1rem;
}

.infoCircle:hover{
	color: #ffff00;
	background-color: #142b68;
	border-radius: 50%;
}
/* End of Header Styling */

/* Loading Screen Styling */

.loadingScreen{
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	background-color: #142b68;
	color: #edfeff;
	text-align: center;
	z-index: 10;
}

/* Start of Pokemon Player Component */

.player{
	position: absolute;
	top: 23%;
	left: 45%;
	font-weight: 800;
	color: #edfeff;
	font-size: 1.1rem;
}

.pokemonPlayer{
  background-color: rgb(20, 43, 104, 0.5);
  background-image: linear-gradient(120deg, rgb(20, 43, 104, 0.5) 50%, rgba(45, 67, 126, 0.5) 50%);
	text-align: center;
	font-family: 'Montserrat', sans-serif;
	text-transform: uppercase;
	display: flex;
	min-height: 75vh;
}

.pokemonPlayer p {
  color: #ffff00;
}

.pokemonPlayer>p {
  
  border-radius: 50%;
  background-color: brown;
  font-size: 2rem;
  padding: 10px;
  -webkit-transform: translate(50%);
          transform: translate(50%);
}



.playerPokemonContainer{
	width: 50%;
	padding: 2% 0;
	display: flex;
  flex-direction: row;
  position: relative;
  justify-content: center;
  
  

}

.playerPokemonDiv {
  width: 50%;
  display: none;
  /* display: flex; */
  
  flex-direction: column;
  justify-content: center;

}



.playerPokemonContainer .pokemon{
  margin: 0 auto;
  /* display: none; */
	/* background-color: #142b68; */
	border-radius: 50%;
  width: 70%;
  -webkit-animation: moveSecond both alternate ease-in-out infinite 2s;
          animation: moveSecond both alternate ease-in-out infinite 2s;
}

.playerPokemonContainer:first-child .pokemon {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  -webkit-animation: moveFirst both alternate ease-in-out infinite 2.5s;
          animation: moveFirst both alternate ease-in-out infinite 2.5s;
}

.imageShadow{
	width: 30%;
	background-color: black;
	margin:0 auto;
	opacity: 0.5;
	height: 20px;
	border-radius: 50%;
	margin-top: 1%;
}

.imageShadow {
  -webkit-animation: shadow both alternate ease-in-out infinite 2s;
          animation: shadow both alternate ease-in-out infinite 2s;
}

.playerPokemonContainer:first-child .imageShadow {
  -webkit-animation: shadow both alternate ease-in-out infinite 2.5s;
          animation: shadow both alternate ease-in-out infinite 2.5s;
}

.pokemonPlayer p:last-of-type {
	top: 35%;
	right: 50%;
}


.gameBoard {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  
}

.gameBoard {
	position: absolute;
	min-height: 12.5vh;
}

.gameBoard>div{
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}

.playerOptions {
  display: flex;
  position: fixed;
  bottom: 3%;
  left: 38%;
  width: 25%;
  min-width: 300px;
  padding: 10px;
  border-radius: 20px;
  border: solid 5px #142b68;
  background-color: #edfeff;
  
}

.playerOptions button{
	padding: 4%;
	background-color: #142b68;
	color: #edfeff;
	width: 100%;
	border-radius: 15px;
	text-transform: uppercase;
  margin:1%;
  border: none;
	font-family: 'Montserrat', sans-serif;
  font-style: oblique;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.playerOptions button:enabled:hover, .playerOptions button:enabled:focus{
	background-color: #ffff00;
	color: #142b68;
	cursor: pointer;
}

/* Start of player Container component styling */

.playerContainer {
  width: 40%;
  
}

.playerCards {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  align-content: flex-start;
  flex-wrap: wrap;
  min-height: 50vh;
}

/* Start of Cards Component styling */


.card {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-transition: all 1s;
  transition: all 1s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}



.card-container {
  min-height: 100px;
  -webkit-perspective: 800px;
          perspective: 800px;
  -webkit-transition: all 1s;
  transition: all 1s;
  flex-grow:0;
  flex-shrink:1;
  flex-basis:calc(50% - 10px);
  margin: 5px;
}

.card {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-animation: deal both ease-out 1 1.5s;
          animation: deal both ease-out 1 1.5s;
}

 .card-front {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  -webkit-transition: all 1s;
  transition: all 1s;
}

.card-front.flipable {
  -webkit-animation: flipFront 1s both 1 linear;
          animation: flipFront 1s both 1 linear;
}

.card-back.flipable {
  -webkit-animation: flipBack 1s both 1 linear;
          animation: flipBack 1s both 1 linear;
}

.card-back.hidden {
  -webkit-animation: flipFront 1s both 1 linear;
          animation: flipFront 1s both 1 linear;
}

.card-front.hidden {
  -webkit-animation: flipBack 1s both 1 linear;
          animation: flipBack 1s both 1 linear;
}

.card-back,
.back, 
.card-front {
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.card-back,
.back,
.card-back img,
.card-front {
  width: 100%;
} 

.flipable.clean {
  animation: deal both alternate-reverse ease-out 1 0.8s;
}

.card-front img {
  height: 100%;
  width: 100%;
} 

/* End of card styling */

 .playerPokemonContainer:first-child .pokeball {
  -webkit-animation: pokeballLeft both alternate linear 1 1.3s;
          animation: pokeballLeft both alternate linear 1 1.3s;
  
  
}

.playerPokemonContainer:nth-child(2) .playerContainer {
  order: 1;
}



.playerPokemonContainer .pokeball {
  -webkit-animation: pokeballRight both alternate linear 1 1.3s;
          animation: pokeballRight both alternate linear 1 1.3s;
  width: 40px;
  height: 40px;
  position: absolute;
}


.whiteDiv {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  -webkit-animation: light forwards 1 1s linear;
          animation: light forwards 1 1s linear;
  background-color: white; 

}

.playerPokemonContainer .pokemonText {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  padding: 10px;
  width: 50%;
  color: #142b68;
  border: solid 5px #142b68;
  background-color: whitesmoke;
  border-radius: 20px;
  -webkit-animation: appear forwards 1 1s linear;
          animation: appear forwards 1 1s linear;
  
}

.healthBar {
  width: 60%;
  height: 20px;
  border-radius: 20px;
  margin: 30px auto 0;
  display: none;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  overflow: hidden;
  position: relative;
  border: solid 3px #142b68;
  background-color: rgba(226, 226, 226, 0.616);
}

.healthBar>div {
  position: absolute;
  left: -10%;
  height: 100%;
  -webkit-transition: all ease-out 1s;
  transition: all ease-out 1s;
  -webkit-transform: skew(-30deg);
          transform: skew(-30deg);
}

.noHealth {
  /* background-image: linear-gradient(90deg, rgb(255, 255, 255), black 100%); */
  background-color: #edfeff;
  width: 10%;
  
}

.halfHealth {
  /* background-image: linear-gradient(120deg, rgb(136, 7, 7) 50%, black 50%); */
  background-color: rgb(114, 12, 12);
  width: 60%;
  position: absolute;
  left: 0;
  

}

.fullHealth {
  /* background-image: linear-gradient(90deg, rgb(38, 119, 17) 100%, black); */
  background-color: seagreen;
  width: 110%;
  left: 0;
  position: absolute;
}

.playerPokemonContainer:first-child .attacking, .playerPokemonContainer .attacking {
  -webkit-animation: attack linear both 1 1s;
          animation: attack linear both 1 1s;
}

.playerPokemonContainer .damaged, .playerPokemonContainer:first-child .damaged {
  -webkit-animation: hit linear 1 1s;
          animation: hit linear 1 1s;
}

@-webkit-keyframes shake {
  0% { -webkit-transform: translate(0, 0) rotate(0); transform: translate(0, 0) rotate(0); }
  20% { -webkit-transform: translate(-10px, 0) rotate(-20deg); transform: translate(-10px, 0) rotate(-20deg); }
  30% { -webkit-transform: translate(10px, 0) rotate(20deg); transform: translate(10px, 0) rotate(20deg); }
  50% { -webkit-transform: translate(-10px, 0) rotate(-10deg); transform: translate(-10px, 0) rotate(-10deg); }
  60% { -webkit-transform: translate(10px, 0) rotate(10deg); transform: translate(10px, 0) rotate(10deg); }
  100% { -webkit-transform: translate(0, 0) rotate(0); transform: translate(0, 0) rotate(0); }
}

@keyframes shake {
  0% { -webkit-transform: translate(0, 0) rotate(0); transform: translate(0, 0) rotate(0); }
  20% { -webkit-transform: translate(-10px, 0) rotate(-20deg); transform: translate(-10px, 0) rotate(-20deg); }
  30% { -webkit-transform: translate(10px, 0) rotate(20deg); transform: translate(10px, 0) rotate(20deg); }
  50% { -webkit-transform: translate(-10px, 0) rotate(-10deg); transform: translate(-10px, 0) rotate(-10deg); }
  60% { -webkit-transform: translate(10px, 0) rotate(10deg); transform: translate(10px, 0) rotate(10deg); }
  100% { -webkit-transform: translate(0, 0) rotate(0); transform: translate(0, 0) rotate(0); }
}

@-webkit-keyframes attack{
    10%,30%,50%,70%,90% {
        -webkit-transform: translate(25%) rotateY(0deg);
                transform: translate(25%) rotateY(0deg);
    }

    0%,20%,40%,60%,80%,100%{
        -webkit-transform: translate(-25%) rotateY(180deg);
                transform: translate(-25%) rotateY(180deg);
    }
}

@keyframes attack{
    10%,30%,50%,70%,90% {
        -webkit-transform: translate(25%) rotateY(0deg);
                transform: translate(25%) rotateY(0deg);
    }

    0%,20%,40%,60%,80%,100%{
        -webkit-transform: translate(-25%) rotateY(180deg);
                transform: translate(-25%) rotateY(180deg);
    }
}

@-webkit-keyframes hit{
    10%,30%,50%,70%,90% {
        opacity: 1;
    }

    0%,20%,40%,60%,80%,100%{
        opacity: 0;
    }
}

@keyframes hit{
